$(".nav-link__show").click(function () {
    $(".nav-link").removeClass("_active")
    $(this).parents(".nav-link").toggleClass("_active")
})


$(document).mouseup(function(e) {
    var div = $('.nav-link');
    if (!div.is(e.target) && div.has(e.target).length === 0) {
        if($(div).hasClass("_active")){
            $(".nav-link").removeClass("_active")
        }
    }
});
